.nav {
    display: flex;
    justify-content: center;
}

.nav ul {
    display: grid;
    grid-template-columns: 65vw 10vw;
    align-items: center;
    margin: .4%;

}

.nav img {
    width: 3vw;
}

#browse {
    color: black;
    text-decoration: none;
}

#browse:hover {
    color: darkmagenta;
    transition: .3s ease-in-out;
}

.nav li {
    list-style: none;
}

.nav hr {
    display: flex;
    justify-content: center;
    width: 90vw;
    margin-top: 0%;
}